___CSS_LOADER_EXPORT___.push([module.id, `:root {
    --text-color: #f5f5f5;
    --bg-color: #171717;
    --muted-bg-color: #1c1c1c;

    --primary-color: #858585;
    --secondary-color: #1c282A;
    --accent-color: #527e86;

    --outline-color: #2b2b2b;

    --default-transition: all 0.4s ease-in-out;

    --primary-font: 'Inter', sans-serif;
    --secondary-font: 'Roboto Serif', serif;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    background-color: var(--bg-color);
    color: var(--text-color);
    font-family: var(--primary-font);

    display: grid;
    grid-template-columns: auto 1fr;
    min-height: 100vh;
}

nav {
    width: clamp(260px, 20vw, 270px);
    background-color: var(--muted-bg-color);
    border-right: 1px solid var(--outline-color);
    text-align: left;
    padding: 1.5rem;

    transition: var(--default-transition);
}

nav * { transition: var(--default-transition); }

nav header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

#header-profile {
    display: flex;
    align-items: center;
    gap: 1rem;
}

#header-profile h1 { font-size: 1rem; }

#header-profile p {
    color: var(--primary-color);
    font-size: 0.8rem;
}

#header-profile img {
    width: 40px;
    height: 40px;
    border-radius: 100%;
    border: 1px solid var(--outline-color);
}

#shrink-nav-button {
    background-color: var(--muted-bg-color);
    border: 1px solid var(--outline-color);
    border-radius: 100%;
    width: 35px; 
    height: 35px; 

    display: flex;
    justify-content: center;

    position: relative;
    left: 2.6rem;
}

#shrink-nav-button:hover {
    cursor: pointer;
    transform: scale(1.1);
}

#shrink-nav-button svg {
    fill: var(--primary-color);
    width: 20px;
    vertical-align: middle;
}

#shrink-nav-button:hover svg { fill: var(--text-color); }

nav.hidden { 
    width: 80px; 
    padding: 1.5rem 1.5rem;
}

nav.hidden #header-profile img {
    width: 30px;
    height: 30px;
}

nav.hidden #shrink-nav-button { left: 0.8rem; }
nav.hidden #header-profile div { display: none; }
nav.hidden #shrink-nav-button svg { transform: rotate(180deg); }

#main-nav-links {
    margin: 2rem 0;

    display: flex;
    flex-direction: column;
}

nav button {
    color: var(--primary-color);
    background: none;
    border: none;
    border-radius: 10px;
    padding: 8px 10px;

    font-size: 0.9rem;

    display: flex;
    align-items: center;
    gap: 1rem;
}

nav button.active {
    background: rgba(255, 255, 255, 0.1);
    color: var(--text-color);
}

nav button svg {
    width: 20px;
    height: 20px;
    fill: var(--primary-color);
    vertical-align: middle;
}

nav button:hover {
    cursor: pointer;
    color: var(--text-color);
}

nav button:hover svg {
    fill: var(--text-color);
}

footer {
    height: 50px;
    background-color: var(--muted-bg-color);

    display: flex;
    flex-direction: row;
    justify-content: center; 
    align-items: center; 
    gap: 7px;
}

footer svg {
    fill: var(--text-color);
    width: 1.5rem;
    transition: var(--default-transition);
    vertical-align: middle;
}

footer svg:hover {
    fill: var(--primary-color);
    cursor: pointer;
}

/* article styles */
article { 
    padding: 8rem 15% 0 15%; 

    display: grid;
    grid-template-rows: auto 1fr auto;
}

article #header-profile { display: none; }

#home-heading { 
    font-size: clamp(2rem, 5vw, 3rem); 
}

#home-heading span { 
    color: #595959; 
    background-image: linear-gradient(0deg, #595959 , #D4D4D4 100%); 
    background-clip: text; 
    -webkit-background-clip: text; 
    -webkit-text-fill-color: transparent;  
}

/* mobile user styles */
@media only screen and (max-width: 900px) {
    body {
        min-height: 100vh;
        grid-template-columns: 1fr;
    }

    nav {
        height: 50px;
        width: 100%;

        position: fixed;
        bottom: 0;
    }

    #shrink-nav-button { display: none; }
    #header-profile { display: none; }

    article { 
        padding: 2rem 10% 0 10%;
        margin-bottom: 50px; 
    }

    article #header-profile {
        display: flex;
        margin-bottom: 2rem;
    }
}`, "",{"version":3,"sources":["webpack://./src/styles/home.css"],"names":[],"mappings":"AAEA;IACI,qBAAqB;IACrB,mBAAmB;IACnB,yBAAyB;;IAEzB,wBAAwB;IACxB,0BAA0B;IAC1B,uBAAuB;;IAEvB,wBAAwB;;IAExB,0CAA0C;;IAE1C,mCAAmC;IACnC,uCAAuC;AAC3C;;AAEA;IACI,sBAAsB;IACtB,SAAS;IACT,UAAU;AACd;;AAEA;IACI,iCAAiC;IACjC,wBAAwB;IACxB,gCAAgC;;IAEhC,aAAa;IACb,+BAA+B;IAC/B,iBAAiB;AACrB;;AAEA;IACI,gCAAgC;IAChC,uCAAuC;IACvC,4CAA4C;IAC5C,gBAAgB;IAChB,eAAe;;IAEf,qCAAqC;AACzC;;AAEA,QAAQ,qCAAqC,EAAE;;AAE/C;IACI,aAAa;IACb,mBAAmB;IACnB,8BAA8B;IAC9B,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,SAAS;AACb;;AAEA,qBAAqB,eAAe,EAAE;;AAEtC;IACI,2BAA2B;IAC3B,iBAAiB;AACrB;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,mBAAmB;IACnB,sCAAsC;AAC1C;;AAEA;IACI,uCAAuC;IACvC,sCAAsC;IACtC,mBAAmB;IACnB,WAAW;IACX,YAAY;;IAEZ,aAAa;IACb,uBAAuB;;IAEvB,kBAAkB;IAClB,YAAY;AAChB;;AAEA;IACI,eAAe;IACf,qBAAqB;AACzB;;AAEA;IACI,0BAA0B;IAC1B,WAAW;IACX,sBAAsB;AAC1B;;AAEA,+BAA+B,uBAAuB,EAAE;;AAExD;IACI,WAAW;IACX,sBAAsB;AAC1B;;AAEA;IACI,WAAW;IACX,YAAY;AAChB;;AAEA,gCAAgC,YAAY,EAAE;AAC9C,iCAAiC,aAAa,EAAE;AAChD,oCAAoC,yBAAyB,EAAE;;AAE/D;IACI,cAAc;;IAEd,aAAa;IACb,sBAAsB;AAC1B;;AAEA;IACI,2BAA2B;IAC3B,gBAAgB;IAChB,YAAY;IACZ,mBAAmB;IACnB,iBAAiB;;IAEjB,iBAAiB;;IAEjB,aAAa;IACb,mBAAmB;IACnB,SAAS;AACb;;AAEA;IACI,oCAAoC;IACpC,wBAAwB;AAC5B;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,0BAA0B;IAC1B,sBAAsB;AAC1B;;AAEA;IACI,eAAe;IACf,wBAAwB;AAC5B;;AAEA;IACI,uBAAuB;AAC3B;;AAEA;IACI,YAAY;IACZ,uCAAuC;;IAEvC,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,mBAAmB;IACnB,QAAQ;AACZ;;AAEA;IACI,uBAAuB;IACvB,aAAa;IACb,qCAAqC;IACrC,sBAAsB;AAC1B;;AAEA;IACI,0BAA0B;IAC1B,eAAe;AACnB;;AAEA,mBAAmB;AACnB;IACI,uBAAuB;;IAEvB,aAAa;IACb,iCAAiC;AACrC;;AAEA,0BAA0B,aAAa,EAAE;;AAEzC;IACI,iCAAiC;AACrC;;AAEA;IACI,cAAc;IACd,+DAA+D;IAC/D,qBAAqB;IACrB,6BAA6B;IAC7B,oCAAoC;AACxC;;AAEA,uBAAuB;AACvB;IACI;QACI,iBAAiB;QACjB,0BAA0B;IAC9B;;IAEA;QACI,YAAY;QACZ,WAAW;;QAEX,eAAe;QACf,SAAS;IACb;;IAEA,qBAAqB,aAAa,EAAE;IACpC,kBAAkB,aAAa,EAAE;;IAEjC;QACI,uBAAuB;QACvB,mBAAmB;IACvB;;IAEA;QACI,aAAa;QACb,mBAAmB;IACvB;AACJ","sourcesContent":["